/* Aqu√≠ van tus estilos */
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.footer{
        margin-top: 1.5rem;
        background: linear-gradient(90deg, rgba(58, 142, 180, 1) 0%, rgba(253, 29, 29, 1) 50%, rgba(252, 176, 69, 1) 100%);
        font-family: Arial, Helvetica, sans-serif;
        display: flex;
        justify-content: space-around;
        padding: 1rem 0;
        font-size: 0.6rem;

    @media(min-width:768px){
        padding: 1rem;
        font-size: 0.9rem
    }
}

.header{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 0.8rem;
    &__logo {
        width: 70vw;
    }

    &__form {
        padding: 0.5rem 0.5rem 0rem 0.5rem;
        letter-spacing: 0.1rem;
        background: linear-gradient(90deg, rgba(58, 142, 180, 1) 0%, rgba(253, 29, 29, 1) 50%, rgba(252, 176, 69, 1) 100%);
        text-transform: uppercase;
        font-weight: bold;
        display: flex;
        flex-direction: column;
        gap:0.8rem;
        width: 100vw;

        &_label{
            display: flex;
            flex-direction: column;
            gap:0.5rem;
        }

        &_input {
            margin-top: 1rem;
            border: none;
            border-radius: 10px;
            padding: 5px;
            margin: 0px 15px;
            background-color: #e8e8e8;
            box-shadow: 4px 4px 8px #000000, -4px -4px 8px #400000;
            font-size: 0.8rem;
            max-width: 90vw;
        }

        &_input:focus {
            outline-color: white;
        }
        .select{width:100px}
    }

    @media(min-width:768px){
        &__logo {
                width: 40vw;
            }
        
            &__form {
                padding: 1rem 1rem 0rem 1rem;
                flex-direction: row;
                &_label{
                    flex-direction: row;
                    align-items: center;
                    gap: 1rem;
                }
                &_input {
                    font-size: 1rem;
                    width: 300px;
                    box-shadow: 6px 6px 12px #000000, -6px -6px 12px #400000;
                }
            }
    }
    @media(min-width: 1200px){
        &__logo{
            width: 20vw;
        }
        &__form{
            padding-top: 1.5rem;
            width: 90vw;
        }
    }
}

.addQuote{
    padding: 0.5rem;
    letter-spacing: 0.1rem;
    background: linear-gradient(90deg, rgba(58, 142, 180, 1) 0%, rgba(253, 29, 29, 1) 50%, rgba(252, 176, 69, 1) 100%);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 0.8rem;
    &_title{
        padding-top: 1rem;
        text-transform: uppercase;
        font-size: 1rem;
        margin: auto;
    }
    &_form{
        padding: 0.5rem 0.5rem 0rem 0.5rem;
        letter-spacing: 0.1rem;
        text-transform: uppercase;
        font-weight: bold;
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        
        &_label{
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }
        &_input{
            border: none;
            border-radius: 10px;
            padding: 5px;
            background-color: #e8e8e8;
            box-shadow: 6px 6px 12px #000000, -6px -6px 12px #400000;
            font-size: 0.8rem;
        }
        .select{
            max-width: 100px;
        }
        &_btn{
            font-size: 1rem;
            padding: 0rem 0.8rem;
            box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.24);
            border-radius: 15px;
        }
        &_btn:hover {
            color: white;
            background-color: #000000;
            box-shadow: 1px 1px 1px 5px rgba(0, 0, 0, 0.24);
        }
    }
    @media(min-width:768px){
            padding: 1rem;
                &_form {
                    padding-top: 1rem;
                    gap: 1rem;
                    display: flex;
                    flex-direction: row;
      
                    &_label {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                    }
            
                    &_input {
                        box-shadow: 6px 6px 12px #000000, -6px -6px 12px #400000;
                        font-size: 1rem;
                        width: 50vw;
                    }
            
                    &_btn:hover {
                        color: white;
                        background-color: #000000;
                        box-shadow: 1px 1px 1px 5px rgba(0, 0, 0, 0.24);
                    }
                }
        
    }
    @media(min-width:1200px){
        width:90vw;
        margin: auto;
        padding-bottom: 1.5rem;

    }
}

.main{
    &_quotes{
        &_ul{
            display: flex;
            flex-direction: column;
        }
    }
    @media(min-width:1200px){
        &_quotes{
            margin: 0 2vw 0 4vw;
        }
    }
}


.listLi{
    display: inline-block;
    width: 100vw;
    padding: 0.5rem;
    
    &__quote {
        font-style: oblique;
        font-size: 0.8rem;
        margin-top: 0.5rem;
        }
    
        &__name {
            text-transform: uppercase;
            padding-top: 0.15rem;
            border-bottom: 2px solid rgb(152, 152, 152);
        }

    @media(min-width:768px){
    &__quote {
            font-size: 1rem;
            margin-top: 0.7rem;
        }
    
        &__name {
            padding-top: 0.5rem;
        }
    }

    @media(min-width: 1200px){
        width: 45vw;
        
        &__quote {
            font-size: 1.1rem;
        }
    }
    
}
